.teachers {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}

.teachers-click-style {
  min-height: calc(80vh - 9.5rem);
}

.teachers h2 {
  margin: 2rem 0;
  font-size: 34px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
}

.teachers .teachers-card .img-box{
  overflow: hidden;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.teachers .teachers-card:hover .text-box {
  background-color: #fff;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.teachers .teachers-card:hover .img-box img {
  transform: scale(1.08);
}

.teachers .teachers-card .img-box img {
  width: 100%;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
  transition: all 0.2s linear;
}

.teachers .teachers-card .text-box {
  background-color: #edf4f6;
  padding: 1.6rem;
  transition: all 0.2s linear;
  border-bottom-left-radius: 7px;
  border-bottom-right-radius: 7px;
}

.teachers .teachers-card .text-box .date {
  font-size: 12px;
  color: #484848;
}

.teachers .teachers-card .text-box h3 {
  font-size: 17px;
  font-family: "Poppins", sans-serif;
  font-weight: 600;
  margin: 1rem 0;
}

@media screen and (max-width: 767px) {
  .teachers {
    align-items: center;
    padding: 1.5rem;
  }
}
